<template>
  <div>
    <img :src="url"  preview="1" style="width:100%">
  </div>
</template>

<script>
export default {
  data() {
    return {
      url: "",
      
    };
  },
  mounted(){
      this.url= this.$store.state.apiurl+ "/upload/"+this.$store.state.image
  }
};
</script>
