<template>
  <div>
    <div id="ca">
      <el-card class="box-card" style="width: 450px">
        <h4 style="text-align: center">请输入对应号码进入评分</h4>
        <div>
          <span>裁判号：</span>
          <el-input
            v-model="tid"
            type="number"
            placeholder="请输入裁判号"
            style="width: 80%"
          ></el-input>
        </div>
        <div style="margin-top: 20px">
          <span>选手号：</span>
          <el-input
            type="number"
            v-model="sid"
            placeholder="请输入选手号"
            style="width: 80%"
          ></el-input>
        </div>
        <div style="margin-top: 20px">
          <span>工位号：</span>
          <el-input
            v-model="sta"
            placeholder="请输入工位号"
            style="width: 80%"
          ></el-input>
        </div>
        <div style="margin-top: 15px; text-align: center">
          <el-button type="primary" plain style="width: 280px" @click="toScore"
            >进入评分</el-button
          >
        </div>
      </el-card>
    </div>
  </div>
</template>


<script>
import axios from "axios";
import Qs from "qs";
export default {
  data() {
    return {
      apiurl: "",
      tid: "",
      sid: "",
      sta: "",
      con: "",
      conteststatus:''

    };
  },
  computed:{
    date(){
      return this.$store.state.date
    },
    start_date(){
      return this.$store.state.start_date
    },
    end_date(){
      return this.$store.state.end_date
    },
    datevalu(){
      return this.$store.state.datevalue
    },
    isstop(){
      return this.$store.state.isstop
    },
    stoptime(){
      return this.$store.state.stoptime
    }
  },
  mounted() {
    this.apiurl = this.$store.state.apiurl;
    this.getcontext();


      // eslint-disable-next-line no-unused-vars
      var stime =setInterval(()=>{
        if (Date.parse(this.start_date)>new Date()){
          this.conteststatus=0
        }else if (Date.parse(this.end_date)<new Date()){
          this.conteststatus=1
        }else if(Date.parse(this.start_date)<new Date()<Date.parse(this.end_date)) {
          this.conteststatus = 3
        }
        if (this.$store.state.isstop===false){
          if(this.conteststatus===0){
            // eslint-disable-next-line no-undef
            this.$store.state.date=(this.start_date).substring(10,20)+" 开始比赛"
          }else if (this.conteststatus===1){
            this.$store.state.date="已结束"
          }else {
            this.$store.state.date=this.formatSeconds(Date.parse(this.end_date)/1000 -  Date.parse(new Date())/1000+this.$store.state.stoptime)
          }


        }
      }, 1000)




  },
  methods: {
    getcontext() {
      axios({
        url: this.apiurl + "/getcontest/",
      }).then((res) => {
        this.con = res.data.id;
        this.$store.state.image = res.data.image
        this.$store.state.start_date = res.data.start_date
        this.$store.state.end_date = res.data.end_date
        if (Date.parse(this.start_date)>new Date()){
          this.$store.state.date="待开始"
          this.$message.error("竞赛尚未开始")
          this.conteststatus=0
        }else if (Date.parse(this.end_date)<new Date()){
          this.$message.error("竞赛已结束")
          this.conteststatus=1
        }else if(Date.parse(this.start_date)<new Date()<Date.parse(this.end_date)){
          this.conteststatus=3
          console.log(this.con);
          // eslint-disable-next-line no-undef
          console.log((this.end_date))
          console.log(Date.parse(new Date()))


        }

      });
    },
  formatSeconds(t) {
    if (!t) return
    if (t < 60) return '00:' + ((i = t) < 10 ? '0' + i : i)
    if (t < 3600) return '' + ((a = parseInt(t / 60)) < 10 ? '0' + a : a) + ':' + ((i = t % 60) < 10 ? '0' + i : i)
    if (t >= 3600) {
      var a; var i; var e = parseInt(t / 3600)
      return (e < 10 ? '0' + e : e) + ':' + ((a = parseInt(t % 3600 / 60)) < 10 ? '0' + a : a) + ':' + ((i = t % 60) < 10 ? '0' + i : i)
    }
},
    subStart() {
      var sid = this.sid;
      var con = this.con;
      var tid = this.tid;
      var sta = this.sta;
      var score = 0;
      axios({
        url: this.$store.state.apiurl + "/posttotal/",
        type: "json",
        method: "post",
        data: Qs.stringify({
          sid,
          con,
          tid,
          sta,
          score,
        }),
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      }).then((res)=>{
        console.log(res.data);
        if (res.data == "ok"){
            this.$store.state.timeshow=true
           this.$router.push({ path: "/score" });
        }else{
          this.$message.error("提交初始成绩错误");
        }
      });
    },
    toScore() {
      if (this.tid == "") {
        this.$message.error("请输入裁判号");
      } else if (this.sid == "") {
        this.$message.error("请输入选手号");
      } else if (this.sta == "") {
        this.$message.error("请输入工位号");
      } else if (this.conteststatus===0){
        this.$message.error("竞赛尚未开始")
      }else if (this.conteststatus===1){
        this.$message.error("竞赛已结束")
      }else {
        axios({
          url: this.apiurl + "/getjobtotal/?sid=" + this.sid + "&con=" + this.con+"&tid="+this.tid,
          type: "json",
          method: "get",
        }).then((res) => {
          console.log(res.data);
          if (res.data == "None") {
            this.$store.state.tid = this.tid;
            this.$store.state.sid = this.sid;
            this.$store.state.sta = this.sta;
            this.$store.state.context = this.con;
            this.$conn(this.tid,this.sid)
            this.subStart()
          }else if(res.data=="error"){
            this.$message.error("裁判号或选手号不正确");
          }else if(res.data.score!="" || res.data.score==0){
            this.$message.error("此选手已存在成绩")
          }
        });
      }
    },
  },
};
</script>

<style scoped>
#ca {
  background-color: #fff;
  border-radius: 20px;
  width: 450px;
  height: 350px;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
</style>