<template>
  <div>
    <div class="flex">
      <vue-signature ref="s1" :width="sWidth" :height="sHeight" />
    </div>
    <div style="text-align: center; margin-top: 10px">
      <el-button
          type="danger"
          icon="el-icon-delete"
          style="margin-right: 50px"
          @click="clean"
      >清空</el-button
      >
      <el-button
          type="primary"
          icon="el-icon-check"
          style="padding-left: 10px"
          @click="getSig"
      >确认签名</el-button
      >
    </div>
    <div style="margin-top: 20px; text-align: center">
      <el-button
          type="success"
          round
          style="width: 35%; height: 40px"
          @click="submit"
      >提交成绩</el-button
      >
    </div>
    <div>
      <el-dialog title="确认信息" :visible.sync="dialogVisible" v-loading="loading">
        <div>
          <div>
            <span>选手号：</span><el-input v-model="sid" placeholder="请输入内容" style="width: 50%;margin-left: 10px;"></el-input>
          </div>
        <div style="margin-top: 10px;">
          <span>裁判号：</span><el-input v-model="tid" placeholder="请输入内容" style="width: 50%;margin-left: 10px;"></el-input>
        </div>
          <div style="margin-top: 10px;">
            <span>工位号：</span><el-input v-model="sta" placeholder="请输入内容" style="width: 50%;margin-left: 10px;"></el-input>
          </div>
          <div style="margin-top: 10px;">
            <span>成&nbsp;&nbsp;&nbsp;绩：</span><span style="color: #F56C6C;margin-left: 15px;font-size: 20px;">{{score}}</span>
          </div>
          <div style="margin-top: 10px">
            <span>裁判签名：</span>
          </div>
            <div style="display: flex;justify-content: center;margin-top: 10px;">
              <div style="width: 70%;height: 20%;border-style: dotted;" >
                <img :src="img" style="width: 70%;height: 100px;">
              </div>
            </div>


        </div>

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="subScore"
          >确 定</el-button
          >
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Qs from "qs";
export default {
  name: "signature",
  data() {
    return {
      sWidth: 800,
      sHeight: 400,
      img: "",
      dialogVisible:false,
      sid: "",
      tid: "",
      sta: "",
      context: "",
      score:0,
      numTableValue: [],
      loading:false,
    };
  },
  mounted() {
    this.$store.state.navshow=false
    this.$store.state.timeshow=false
    this.sid = this.$store.state.sid;
    this.tid = this.$store.state.tid;
    this.sta = this.$store.state.sta;
    this.context = this.$store.state.context;
    this.score = this.$store.state.score;
    this.numTableValue = this.$store.state.numTableValue;

    let _this = this;
    _this.sWidth = window.screen.availWidth - window.screen.availWidth * 0.3;
    _this.sHeight = window.screen.availHeight - window.screen.availHeight * 0.4;
    window.onresize = function () {
      _this.sWidth = window.screen.availWidth - window.screen.availWidth * 0.3;
      _this.sHeight =
          window.screen.availHeight - window.screen.availHeight * 0.4;
    };
  },
  methods: {
    clean() {
      this.$refs.s1.clear();
    },
    getSig() {
      if (this.$refs.s1.isEmpty() == false) {
        this.img = this.$refs.s1.getSignature();
        this.$message({
          message: "已获取到签名",
          type: "success",
        });

      } else {
        this.$message.error("当前没有签名");
      }
    },

    submit() {
      if (this.$refs.s1.isEmpty() == false){
        this.dialogVisible=true;
        this.img = this.$refs.s1.getSignature();
      }else {
        this.$message.error("当前没有签名");
      }

    },
    subScore() {
      this.loading=true;
      var sid = this.sid;
      var con = this.context;
      var tid = this.tid;
      var sta = this.sta;
      var score = this.score;
      var img = new FormData
      img.append('img',this.img)
      // console.log(this.img)
      axios({
        url: this.$store.state.apiurl + "/posttotal/",
        type: "json",
        method: "post",
        data: Qs.stringify({
          sid:sid,
          con:con,
          tid:tid,
          sta:sta,
          score: score,
          img:this.img,
        }),

        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      }).then((res)=>{
        console.log(res.data);
        if(res.data=="save"){
          var sid = this.sid;
          var tid = this.tid;
          var sta = this.sta;
          var con = this.context;
          var item = this.numTableValue;
          console.log(item);
          axios({
            url: this.$store.state.apiurl + "/subjobscore/",
            type: "json",
            method: "post",
            data: Qs.stringify({
              sid,
              tid,
              con,
              sta,
              item: JSON.stringify(item),
            }),
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
            },
          }).then((res) => {
            console.log(res.data);
            if (res.data == "ok" || res.data == "save") {
              this.loading=false
              this.$message({
                type: "success",
                message: "提交成功!",
              });
                setTimeout(()=>{
                  this.$router.push({ path: "/end" });
                },1000)
              // location.reload()
            } else {
              this.$message.error("提交错误");
              this.loading=false
            }
          });

        }else if(res.data=="error"){
          this.$message.error("提交错误")
          this.loading=false
        }


      });
    },
  },
  computed: {},
};
</script>

<style scoped>
.flex {
  display: flex;

  justify-content: center;

  width: 100%;
  /*height: 100vh;*/
}
</style>