<template>
  <div>
    <div>
      <h3 style="text-align: center">机械拆装评分细则</h3>
    </div>
    <div style="margin-left: 10px; margin-right: 10px">
      <span>选手比赛号：{{ sid }}</span>
       <span style="margin-left:25%">座位号：{{context}}</span>
      <span style="float: right">裁判员号：{{ tid }}</span>
    </div>
    
   
    
    <div style="margin-top: 2px; margin-left: 10px">
      <span style="text-align: center">当前总成绩：</span
      ><span style="color: #f56c6c">{{ score }}</span>
    </div>
    <div style="margin-top: 20px">
      <el-table
        show-summary
        :data="tableData"
        :span-method="objectSpanMethod"
        :highlight-current-row="true"
        border
        style="width: 100%; margin-top: 20px; text-algin: center"
      >
        <el-table-column align="center" prop="job" label="作业项目" width="100">
        </el-table-column>
        <el-table-column header-align="center" prop="item" label="作业小项">
        </el-table-column>
        <el-table-column align="center" width="200" prop="num" label="配分">
          <template slot-scope="scope">
            <el-input-number
              @change="handnum(scope.$index, scope)"
              size="small"
              v-model="scope.row.num"
              :disabled="enInput"
              :precision="1"
              :step="0.5"
              :max="scope.row.score"
              :min="scope.row.score * -1"
            ></el-input-number>
          </template>
        </el-table-column>
        <el-table-column label="分值" width="100" align="center" prop="score">
        </el-table-column>
      </el-table>
    </div>
    <div style="margin-top: 10px">
      <el-button type="primary" icon="el-icon-arrow-left" @click="back"
        >上一模块</el-button
      >
      <el-button type="primary" style="float: right" @click="next"
        >下一模块<i class="el-icon-arrow-right el-icon--right"></i
      ></el-button>
    </div>
    <div style="margin-top: 20px; text-align: center">
      <el-button
        v-show="showSubmit"
        type="success"
        round
        style="width: 100%; height: 40px"
        @click="submit"
        >结束评分</el-button
      >
    </div>
  </div>
</template>



<script>
import axios from "axios";
// import Qs from "qs";
export default {
  data() {
    return {
      enInput: false,
      showSubmit: false,
      tableData: [],
      table: "",
      numTable: [],
      numTableValue: [],
      values: [],
      tableIndex: 0,
      score: 0,
      num: 0,
      sid: "",
      tid: "",
      sta: "",
      context: "",
      mscore: [],
      numIndex: [],
    };
  },
  mounted() {

    this.sid = this.$store.state.sid;
    this.tid = this.$store.state.tid;
    this.sta = this.$store.state.sta;
    this.context = this.$store.state.context;
    this.mscore[0] = 0;
    this.getList();
    // eslint-disable-next-line no-unused-vars
    var s = setInterval(()=>{
      if (this.$store.state.date==="已结束"){
        this.$store.state.score = this.score;
        this.$store.state.numTableValue = this.numTableValue;
        this.$router.push({ path: "/signature" });
        clearInterval(s)
      }
    },1000)
  },
  methods: {
    
    getList() {
      axios({
        url: this.$store.state.apiurl + "/getjob/?id="+this.context,
        type: "json",
        method: "get",
      }).then((res) => {
        // console.log(res.data);
        this.table = res.data;
        this.tableData = this.table[this.tableIndex];
        let data2 = this.mergeTableRow(this.tableData, ["job"]);
        this.tableData = data2;
        // this.subStart();
      });
    },
    back() {
      if (this.tableIndex != 0) {
        this.tableIndex = this.tableIndex - 1;
        console.log(this.tableIndex);
        this.tableData = this.table[this.tableIndex];
        let data2 = this.mergeTableRow(this.tableData, ["job"]);
        this.tableData = data2;
        if (this.numTableValue[this.tableIndex] == null) {
          this.numTable = [];
          this.enInput = false;
        } else {
          this.enInput = true;
          this.numTable[this.numTableValue[this.tableIndex]];
        }
      } else {
        this.$message.error("这是第一个模块");
      }
    },
    next() {
      if (this.tableIndex != this.table.length - 1) {
        console.log(this.num);
        if (this.tableIndex != this.table.length - 1) {
          this.tableIndex = this.tableIndex + 1;
          console.log(this.tableIndex);
          this.tableData = this.table[this.tableIndex];
          let data2 = this.mergeTableRow(this.tableData, ["job"]);
          this.tableData = data2;
          if (this.numTableValue[this.tableIndex] == null) {
            this.numTable = [];
            this.enInput = false;
          } else {
            this.enInput = true;
            this.numTable[this.numTableValue[this.tableIndex]];
          }
        }
      } else {
        this.showSubmit = true;
        this.$message.error("这是最后一个模块");
      }
    },
    count() {
      var score = 0;
      for (var i = 0; i < this.mscore.length; i++) {
        if (this.mscore[i] != null) {
          score = score + this.mscore[i];
        }
      }
      console.log("总成绩");
      console.log(score);
      this.score = score;
    },
    submit() {
      this.$confirm("如果您已确认成绩，点击确定进行签名", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          // this.subScore()
          this.$store.state.score = this.score;
          this.$store.state.numTableValue = this.numTableValue;
          this.$router.push({ path: "/signature" });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消提交",
          });
        });
    },
    handnum(index, scope) {
      console.log(index, scope.row.num);

      var data = {
        jobid: this.tableData[index].jobid,
        id: this.tableData[index].id,
        score: scope.row.num,
      };
      console.log(this.numTable);
      console.log("分数");
      console.log(this.mscore[this.tableIndex]);
      console.log(scope.row.num);
      if (this.mscore[this.tableIndex] != null) {
        if (this.numTable[index] != null) {
          if (this.numTable[index].score == null) {
            this.mscore[this.tableIndex] =
              parseFloat(this.mscore[this.tableIndex]) +
              parseFloat(scope.row.num);
            // console.log(this.mscore)
          } else {
            this.mscore[this.tableIndex] =
              parseFloat(this.mscore[this.tableIndex]) -
              parseFloat(this.numTable[index].score) +
              parseFloat(scope.row.num);
            // console.log(this.mscore)
          }
        } else {
          this.mscore[this.tableIndex] =
            parseFloat(this.mscore[this.tableIndex]) +
            parseFloat(scope.row.num);
          // console.log(this.mscore)
        }
      } else {
        this.mscore[this.tableIndex] = scope.row.num;
      }
      this.count();
      this.numTable[index] = data;
      this.numTableValue[this.tableIndex] = this.numTable;

      // this.values[row.num]
    },
    objectSpanMethod({ row, column }) {
      const span = column["property"] + "-span";
      if (row[span]) {
        return row[span];
      }
    },
    /**
     *
     * @param data  表格数据
     * @param merge 需要合并的列字段名称数组
     * @returns {*}
     */
    mergeTableRow(data, merge) {
      if (!merge || merge.length === 0) {
        return data;
      }
      merge.forEach((m) => {
        const mList = {};
        data = data.map((v, index) => {
          const rowVal = v[m];
          if (mList[rowVal] && mList[rowVal].newIndex === index) {
            mList[rowVal]["num"]++;
            mList[rowVal]["newIndex"]++;
            data[mList[rowVal]["index"]][m + "-span"].rowspan++;
            v[m + "-span"] = {
              rowspan: 0,
              colspan: 0,
            };
          } else {
            mList[rowVal] = { num: 1, index: index, newIndex: index + 1 };
            v[m + "-span"] = {
              rowspan: 1,
              colspan: 1,
            };
          }
          return v;
        });
      });
      return data;
    },
  },
};
</script>

